import {UseQueryOptions, UseQueryResult, useQuery} from '@tanstack/react-query';
import baseUrl from '../baseUrl';
import {useCreateAuthenticatedFetch} from '../useCreateAuthenticatedFetch';
import {handleResponse} from '../handleResponse';
import ProblemDetailsError from '../ProblemDetailsError';

export interface TimeZoneInfoModel {
    ianaId: string;
    displayName: string;
    currentUtcOffset: string;
    supportsDaylightSavingTime: boolean;
}

type TimeZonesQueryOptions = Omit<
    UseQueryOptions<
        TimeZoneInfoModel[],
        Error | ProblemDetailsError,
        TimeZoneInfoModel[],
        readonly unknown[]
    >,
    'queryKey' | 'queryFn'
>;

const useGetTimeZones = (
    options?: TimeZonesQueryOptions
): UseQueryResult<TimeZoneInfoModel[], Error | ProblemDetailsError> => {
    const url = `${baseUrl}/api/timezones`;
    const queryKey = ['timezones'];

    const authenticatedFetch = useCreateAuthenticatedFetch();

    const queryFn = async (): Promise<TimeZoneInfoModel[]> => {
        const response = await authenticatedFetch(url);
        return handleResponse(response);
    };

    return useQuery({
        queryKey,
        queryFn,
        staleTime: Infinity,
        ...options
    });
};

export default useGetTimeZones;
