import React from 'react';
import Select from 'react-select';
import {FormGroup, Label, FormText} from 'reactstrap';
import useGetTimeZones from '../../api/application/useGetTimeZones';
import ErrorDisplay from './ErrorDisplay';

interface TimeZoneOption {
    value: string;
    label: string;
}

interface TimeZoneSelectProps {
    value: string;
    onChange: (value: string) => void;
    label?: string;
    className?: string;
    helpText?: string;
}

const TimeZoneSelect: React.FC<TimeZoneSelectProps> = ({
    value,
    onChange,
    label = 'Time Zone',
    className = '',
    helpText
}) => {
    const {
        data: allTimeZones,
        isLoading: isTzLoading,
        error: tzError
    } = useGetTimeZones();

    // Map your time zones to react-select options
    const options: TimeZoneOption[] = allTimeZones
        ? allTimeZones.map((tz: any) => ({
              value: tz.ianaId,
              label: tz.displayName
          }))
        : [];

    // Find the currently selected option
    const selectedOption =
        options.find(option => option.value === value) || null;

    return (
        <FormGroup className={className}>
            <Label htmlFor="timezoneSelect">{label}</Label>
            {isTzLoading && <div>Loading time zones...</div>}
            {tzError && (
                <ErrorDisplay
                    title="Error Loading Time Zones"
                    error={tzError}
                />
            )}
            {!isTzLoading && !tzError && allTimeZones && (
                <Select
                    inputId="timezoneSelect"
                    options={options}
                    value={selectedOption}
                    onChange={option => onChange(option ? option.value : '')}
                    placeholder="(Select Time Zone)"
                    isClearable
                    // The default filtering is case-insensitive,
                    // but you can customize it as needed:
                    filterOption={(option, inputValue) =>
                        option.label
                            .toLowerCase()
                            .includes(inputValue.toLowerCase())
                    }
                />
            )}
            {helpText && <FormText>{helpText}</FormText>}
        </FormGroup>
    );
};

export default TimeZoneSelect;
